module.exports = "body {\n    margin: 0;\n    min-width: 250px;\n  }\n  \n  /* Include the padding and border in an element's total width and height */\n  \n  * {\n    box-sizing: border-box;\n  }\n  \n  /* Remove margins and padding from the list */\n  \n  ul {\n    margin: 0;\n    padding: 0;\n  }\n  \n  /* Style the list items */\n  \n  ul li {\n    cursor: pointer;\n    position: relative;\n    padding: 12px 8px 12px 40px;\n    list-style-type: none;\n    background: #eee;\n    font-size: 18px;\n    transition: 0.2s;\n    \n    /* make the list items unselectable */\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n  }\n  \n  /* Set all odd list items to a different color (zebra-stripes) */\n  \n  ul li:nth-child(odd) {\n    background: #f9f9f9;\n  }\n  \n  /* Darker background-color on hover */\n  \n  ul li:hover {\n    background: #ddd;\n  }\n  \n  /* Style the close button */\n  \n  .close {\n    position: absolute;\n    right: 0;\n    top: 0;\n    padding: 12px 16px 12px 16px;\n  }\n  \n  .close:hover {\n    background-color: #f44336;\n    color: white;\n  }\n  \n  /* Style the header */\n  \n  .header {\n    background-color: #197069;\n    padding: 30px 40px;\n    color: white;\n    text-align: center;\n  }\n  \n  /* Clear floats after the header */\n  \n  .header:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n  \n  /* Style the input */\n  \n  input {\n    margin: 0;\n    border: none;\n    border-radius: 0;\n    width: 75%;\n    padding: 10px;\n    float: left;\n    font-size: 16px;\n  }\n  \n  /* Style the \"Add\" button */\n  \n  .addBtn {\n    padding: 10px;\n    width: 25%;\n    background: #8a880d;\n    color: white;\n    float: left;\n    text-align: center; \n    font-size: 16px;\n    cursor: pointer;\n    transition: 0.3s;\n    border-radius: 0;\n  }\n  \n  .addBtn:hover {\n    background-color: #bbb;\n  }\n/*# sourceMappingURL=data:application/json;base64, *